import React from 'react';
import * as S from './style';
import homeIcon from '../../assets//_.svg';
import recommIcon from '../../assets//_.svg';
import titleIcon from '../../assets//_.svg';
import myPageIcon from '../../assets//_.svg';

interface NavigationProps {
	onClickMove: (url: string) => any;
	navText: string[];
}

export default function NavigationView(props: NavigationProps) {
	return (
		<S.NavBar>
			<S.Figure onClick={() => props.onClickMove('/home')}>
				<S.NavIcon src={homeIcon} alt=" " size="40px" margin="6px 0 2px" />
				<S.NavText>{props.navText[0]}</S.NavText>
			</S.Figure>
			<S.Figure onClick={() => props.onClickMove('/recommend')}>
				<S.NavIcon src={recommIcon} alt="  " size="36px" margin="8px 0 4px" />
				<S.ReviewText>{props.navText[1]}</S.ReviewText>
			</S.Figure>
			<S.Figure onClick={() => props.onClickMove('/title')}>
				<S.NavIcon src={titleIcon} alt=" " size="36px" margin="6px 0 4px" />
				<S.NavText>{props.navText[2]}</S.NavText>
			</S.Figure>
			<S.Figure onClick={() => props.onClickMove('/mypage')}>
				<S.NavIcon src={myPageIcon} alt="  " size="40px" margin="6px 0 2px" />
				<S.NavText>{props.navText[3]}</S.NavText>
			</S.Figure>
		</S.NavBar>
	);
}
