import React from 'react';
import * as S from '../style';
import { Action } from '../../../slice/findStoreSlice';
import store from '../../../assets//.svg';
import menu from '../../../assets//.svg';
import review from '../../../assets//.svg';

export interface StoreProps {
	borderRadius?: string;
	storeData: any;
}

export default function ChainStoreView(props: StoreProps) {
	return (
		<S.LoadText borderRadius={props.borderRadius}>
			<S.TextCont>
				<S.Icon src={store} alt="" />
				<S.Text>{props.storeData.store_name}</S.Text>
			</S.TextCont>
			<S.TextCont>
				<S.Icon src={menu} alt="" />
				<S.Text>{props.storeData.menu_name}</S.Text>
			</S.TextCont>
			<S.TextCont>
				<S.Icon src={review} alt="" />
				<S.Text>{props.storeData.review}</S.Text>
			</S.TextCont>
		</S.LoadText>
	);
}
